<template>
  <div>
    <div class="mailWrap">
        <table class="mail">
            <tr>
                <th>우편번호 검색</th>
            </tr>
            <tr>
                <th>지역명</th>
                <td>
                    <input type="text">
                    <button>검색</button>
                </td>
            </tr>
        </table>
    </div>
  </div>
</template>

<script>

export default {
  name: 'MailPopup',
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mailWrap {
  padding: 10px;
}
.mail {
  width: 400px;
}
.mail > tr > th {
  width: 80px;
  margin-left: 10px;
}
.mail > tr > td {
  width: 200px;
}
.mail > tr:nth-child(1) > th {
  background: #fff;
}
</style>



