<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuPage/>
      </div>
      <div class="section">
        <div class="title">
          <h3>페이지관리</h3>
          <span>페이지를 추가/수정/삭제 관리합니다.</span>
        </div>
        <table>
            <tr>
                <tr>
                <th>페이지코드*</th>
                <td>
                    <input type="text" value="page03">
                </td>
            </tr>
            <tr>
                <tr>
                <th>페이지명*</th>
                <td>
                    <input type="text" value="광고 및 스팸문자정책">
                </td>
            </tr>
            <tr>
                <tr>
                <th>페이지url*</th>
                <td>
                    http://www.munja.com/<input type="text" value="http://munja.com">
                </td>
            </tr>
        </table>
        <div class="editor">
            <Editor/>
        </div>
        <div class="button__section">
          <div>
            <button @click="hyper('PageList')">확인</button>
            <button @click="hyper('PageList')">취소</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuPage from '../SideMenuPage'
import Header from '../Header'
import Editor from '../Editor'

export default {
  name: 'Spam',
  components: {
    SideMenuPage,
    Header,
    Editor
  },
  data () {
    return {}
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 table {
    margin-top: 10px;
 }
 table > tr > th {
     width: 250px;
 }
.tableContent {
    width: 100%;
    text-align: center;
}
.buttonBox {
    width: 100%;
    text-align: center;
    margin: 40px 0;
}
.buttonBox button {
    width: 80px;
    height: 40px;
    margin-right: 20px;
}
.Mail > td input {
    width: 50px;
    margin-right: 10px;
}
.Mail > td button {
    border-radius: 5px;
    width: 100px;
}
.address > input {
    width: 400px;
}
</style>



