t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuInfo/>
      </div>
      <div class="section">
        <div class="title">
          <h3>팝업관리</h3>
          <span>팝업을 추가/수정/삭제 합니다.</span>
        </div>
        <table>
            <tr>
                <th width="5%">번호</th>
                <th width="40%">제목</th>
                <th>공지기간</th>
                <th>등록일</th>
                <th width="10%">기능</th>
            </tr>
            <tr v-for="(q, index) in list" :key="index">
                <td>{{ q.number }}</td>
                <td>{{ q.title }}</td>
                <td>{{ q.day }}</td>
                <td>{{ q.day }}</td>
                <td>
                    <button @click="hyper('popupadmin')">수정</button>
                    <button>삭제</button>
                </td>
            </tr>
        </table>
        <div class="tableBottom">
            <div class="tableBottomLeft">
                <button @click="hyper('popupadmin')">팝업등록</button>
            </div>
        </div>
        <div class="tableBottomPaging">
          <div class="tableBottomCenter">
            <img src="../../assets/btn_prev2.gif">
            <img src="../../assets/btn_prev.gif">
            <p>1/</p>
            <img src="../../assets/btn_next.gif">
            <img src="../../assets/btn_next2.gif">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuInfo from '../SideMenuInfo'
import Header from '../Header'

export default {
  name: 'PopupList',
  components: {
    SideMenuInfo,
    Header
  },
  data () {
    return {
      list: [
        {
          number: '1',
          title: '이벤트팝업',
          day: '2021-06-14 17:00:00'
        },
        {
          number: '1',
          title: '이벤트팝업',
          day: '2021-06-14 17:00:00'
        }
      ]
    }
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
table {
    text-align: center;
}
.tableBottom {
    margin-top: 20px;
    display: flex;
    width: 100%;
}
</style>



