t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMember/>
      </div>
      <div class="section">
        <div class="title">
          <h3>포인트설정</h3>
          <span>활동별 포인트를 관리합니다</span>
        </div>
        <table>
            <tr>
                <th>회원가입 포인트</th>
                <td>
                    <input type="text" value="100">
                </td>
                <th>로그인 포인트</th>
                <td>
                    <input type="text" value="0">
                </td>
            </tr>
            <tr>
                <th>게시판 보기 포인트</th>
                <td>
                    <input type="text" value="0">
                </td>
                <th>게시판 글쓰기 포인트</th>
                <td>
                    <input type="text" value="0">
                </td>
            </tr>
            <tr>
                <th>게시판 다운로드 포인트</th>
                <td>
                    <input type="text" value="0">
                </td>
                <th>게시판 덧글 포인트</th>
                <td>
                    <input type="text" value="0">
                </td>
            </tr>
            <tr>
                <th>게시판 추천 포인트</th>
                <td>
                    <input type="text" value="0">
                </td>
                <th>쪽지 보내기 포인트</th>
                <td>
                    <input type="text" value="0">
                </td>
            </tr>
            <tr class="warning">
                <th>포인트가 없을경우 경고메세지</th>
                <td><input type="text" value="포인트가 부족합니다."></td>
            </tr>
        </table>
        <div class="button__section">
          <div>
            <button @click="hyper('PointList')">확인</button>
            <button @click="hyper('PointList')">취소</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMember from '../SideMenuMember'
import Header from '../Header'

export default {
  name: 'PointSetting',
  components: {
    SideMenuMember,
    Header
  },
  data () {
    return {}
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 table {
    margin-top: 10px;
 }
 table > tr > th {
     width: 250px;
 }
.tableContent {
    width: 100%;
    text-align: center;
}
.warning > td > input {
    width: 300px;
}
</style>



