<template>
  <div class="test">
    <div class="mailWrap">
        <table class="mail">
            <tr>
                <th>주문내역</th>
            </tr>
            <tr>
              <th>은행명</th>
              <td><input type="text"></td>
            </tr>
            <tr>
              <th>계좌번호</th>
              <td><input type="text"></td>
            </tr>
            <tr>
              <th>예금주</th>
              <td><input type="text" value="관리자"></td>
            </tr>
        </table>
        <div class="buttonBox">
            <button>확인</button>
            <button>취소</button>
        </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'BankPopup',
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mail {
  margin-top: 10px;
  width: 370px;
}
.mail > tr > th {
  margin-left: 10px;
}
.mail > tr:nth-child(1) > th {
  background: #fff;
}
.buttonBox {
    width: 100%;
    text-align: center;
    margin: 40px 0;
}
.buttonBox button {
    width: 80px;
    height: 40px;
    margin-right: 20px;
}
</style>



