<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuPost/>
      </div>
      <div class="section">
        <div class="title">
          <h3>게시판관리</h3>
          <span>게시판을 추가/삭제, 상세기능을 설정합니다.</span>
        </div>
        <div class="section__top">
            <div class="section__top__left">
                <span>선택게시판 :</span>
                <select v-model="list" @change="change($event)">
                    <option value="notice">공지사항</option>
                    <option value="qna">1:1상담</option>
                    <option value="taq">자주묻는질문</option>
                </select>
            </div>
            <div class="section__top__right">
                <select>
                    <option value="title">제목</option>
                    <option value="content">내용</option>
                </select>
                <input type="text">
                <button>검색</button>
            </div>
        </div>
        <table>
            <tr>
              <th width="5%"><input type="checkbox"></th>
              <th width="5%">번호</th>
              <th>제목</th>
              <th width="10%">작성자</th>
              <th width="10%">작성일</th>
              <th width="5%">조회</th>
              <th width="10%">기능</th>
            </tr>
            <tr>
              <td colspan="5">작성된 글이 없습니다.</td>
            </tr>
        </table>
        <div class="section__bottom">
          <div>
            <button @click="add">글쓰기</button>
            <button>선택삭제</button>
          </div>
          <div>
            <button>게시물이동</button>
            <button>게시물복사</button>
          </div>
        </div>
        <div class="tableBottomPaging">
          <div class="tableBottomCenter">
            <img src="../../assets/btn_prev2.gif">
            <img src="../../assets/btn_prev.gif">
            <p>1/</p>
            <img src="../../assets/btn_next.gif">
            <img src="../../assets/btn_next2.gif">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../Header'
import SideMenuPost from '../SideMenuPost'

export default {
  name: 'Taq',
  components: {
    Header,
    SideMenuPost
  },
  data () {
    return {
      list: 'taq'
    }
  },
  watch: {
  },
  methods: {
    change (event) {
      this.$router.push(event.target.value)
    },
    add () {
      this.$router.push('PostAdd')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.section__top {
    display: flex;
    justify-content: space-between;
}
.section__top__left > span {
    font-size: 13px;
    font-weight: bold;
}
table {
    margin-top: 10px;
    text-align: center;
}
.section__bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
</style>



