t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMember/>
      </div>
      <div class="section">
        <div class="title">
          <h3>메세지설정</h3>
          <span>상황별 메일/SMS발송 내용을 관리합니다.</span>
        </div>
        <table>
            <tr>
                <tr>
                <th>코드*</th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <tr>
                <th>페이지명*</th>
                <td>
                    <input type="text">
                </td>
            </tr>
        </table>
        <div>
            <Editor/>
        </div>
        <div class="button__section">
          <div>
            <button @click="hyper('MessageSetting')">확인</button>
            <button @click="hyper('MessageSetting')">취소</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMember from '../SideMenuMember'
import Header from '../Header'
import Editor from '../Editor'

export default {
  name: 'AddMessege',
  components: {
    SideMenuMember,
    Header,
    Editor
  },
  data () {
    return {}
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 table {
    margin-top: 10px;
 }
 table > tr > th {
     width: 250px;
 }
 table > tr > td > input {
     width: 400px;
 }
.tableContent {
    width: 100%;
    text-align: center;
}
</style>



