<template>
  <router-view/>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
* {
  margin: 0px;
  padding: 0px;
  outline: none;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}
html, body {
  width: 100%;
  height: auto;
}
table {
  border-collapse: collapse;
  text-align: left;
  width: 100%;
}
th {
  color: #1e291f;
  background: linear-gradient( #c0e4c2, #abd0ae);
  font-size: 13px;
  padding-left: 10px;
  height: 30px;
}
td {
  font-size: 13px;
  height: 30px;
  padding-left: 10px;
}
input {
  padding: 2px 5px;
  border: 1px solid #ccc;
}
input[type="radio"] {
  margin-right: 3px;
}
.radio__items {
  display: flex;
  align-items: center;
}
.radio__items input {
  margin-right: 4px;
}
.radio__items span {
  margin-right: 10px;
}
button {
  background: #90b994;
  border: 0px;
  border-radius: 5px;
  color: #fff;
  padding: 3px 5px;
  cursor: pointer;
}
.button__section {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 15px 0px;
}
.button__section > div > button {
  width: 80px;
  height: 50px;
}
textarea {
  border: 1px solid #cccccc;
}
.wrap {
  width: 100%;
  height: auto;
  padding: 30px;
  min-width: 1500px;
}
.article {
  margin-top: 15px;
  display: flex;
  width: 100%;
  margin: 15px 0;
}
.side {
  width: 100%;
  max-width: 200px;
}
.section {
  width: calc(100% - 200px);
  margin-left: 15px;
}
.title {
  width: 100%;
  margin-bottom: 15px;
  display: flex;
  align-items: flex-end;
}
.title > span {
  margin-left: 10px;
  font-size: 12px;
}
.tableBottomPaging {
  display: flex;
  justify-content: center;
}
.section__middle__left > span {
  font-size: 13px;
  font-weight: bold;
}
.tableBottomCenter {
  display: flex;
  align-items: center;
}
.tableBottomCenter > p {
  margin: 0px 10px;
  cursor: pointer;
  font-size: 14px;
}
.tableBottomCenter > img {
  width: 17px;
  height: 17px;
  cursor: pointer;
}
.editor {
  margin-top: 5px;
}
</style>



