t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuPost/>
      </div>
      <div class="section">
        <div class="title">
          <h3>게시글작성</h3>
          <span>게시글을 작성하여 추가합니다.</span>
        </div>
        <table>
            <tr>
                <th>작성자</th>
                <td class="td__size">
                    <input type="text">
                </td>
                <th>이메일</th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th>작성일</th>
                <td class="td__size">
                    <input type="text">
                </td>
                <th>조회수</th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th rowspan="2">제목</th>
                <td colspan="3">
                    <input type="checkbox">
                    <span>공지글</span>
                    <input type="checkbox">
                    <span>비밀글</span>
                    <input type="checkbox">
                    <span>HTML사용</span>
                </td>
            </tr>
            <tr>
                <td colspan="3">
                    <input type="text" class="title__input">
                </td>
            </tr>
            <tr>
                <th>내용</th>
                <td colspan="3">
                    <Editor/>
                </td>
            </tr>
            <tr>
                <th>비밀번호</th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th>파일첨부1</th>
                <td>
                    <input type="file">
                </td>
            </tr>
            <tr>
                <th>파일첨부2</th>
                <td>
                    <input type="file">
                </td>
            </tr>
        </table>
        <div class="button__section">
            <div>
                <button>확인</button>
                <button @click="cancle">취소</button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuPost from '../SideMenuPost'
import Header from '../Header'
import Editor from '../Editor'

export default {
  name: 'PostAdd',
  components: {
    SideMenuPost,
    Header,
    Editor
  },
  data () {
    return {}
  },
  methods: {
    cancle () {
      this.$router.go(-1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 table {
    margin-top: 10px;
 }
 table > tr > th {
     width: 300px;
 }
 table > tr > td {
     width: 600px;
 }
.td__size {
    width: 500px;
}
.title__input {
    width: 50%
}
</style>



