t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMember/>
      </div>
      <div class="section">
        <div class="title">
          <h3>회원 로그인 로그</h3>
          <span>회원 로그인 로그를 관리합니다.</span>
        </div>
        <table class="search">
          <tr>
            <th>조건검색</th>
            <td>
              <select>
                <option>고객명</option>
                <option>아이디</option>
                <option>로그인 ip</option>
              </select>
              <input type="text">
              <button>검색</button>
            </td>
          </tr>
        </table>
        <div class="tableTop">
          <div class="tabelTopLeft"><span>총 회원수 : 1, 검색 회원수 : 1</span></div>
        </div>
        <table>
            <tr>
              <th width="5%">번호</th>
              <th width="10%">이름</th>
              <th width="10%">아이디</th>
              <th width="37%">로그인 IP</th>
              <th width="37%">로그인 시간</th>
            </tr>
            <tr v-for="(q, index) in list" :key="index">
              <td>{{ q.number }}</td>
              <td>{{ q.name }}</td>
              <td>{{ q.id }}</td>
              <td>{{ q.loginip }}</td>
              <td>{{ q.logintime }}</td>
            </tr>
        </table>
        <div class="tableBottomPaging">
          <div class="tableBottomCenter">
            <img src="../../assets/btn_prev2.gif">
            <img src="../../assets/btn_prev.gif">
            <p>1/</p>
            <img src="../../assets/btn_next.gif">
            <img src="../../assets/btn_next2.gif">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMember from '../SideMenuMember'
import Header from '../Header'

export default {
  name: 'MemberLog',
  components: {
    SideMenuMember,
    Header
  },
  data () {
    return {
      list: [
        {
          number: '1',
          name: '김영주',
          id: 'angelofsky',
          loginip: '222.222.222.222',
          logintime: '2021-06-15 15:00:20'
        }
      ]
    }
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.tableTop {
  width: 100%;
  height: 24px;
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
  margin-bottom: 5px;
}
.tabelTopLeft > span {
  font-size: 13px;
  font-weight: bold;
}
table {
  text-align: center;
}
.search > tr > th {
  width: 200px;
}
.search > tr > td {
  text-align: left;
}
</style>



