t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuInfo/>
      </div>
      <div class="section">
        <div class="title">
          <h3>관리자목록</h3>
          <span>관리자를 추가/수정/삭제 합니다.</span>
        </div>
        <table>
            <tr>
                <th>제목*</th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th>게시기간*</th>
                <td>
                    <input type="text"> ~ <input type="text">
                </td>
            </tr>
            <tr>
                <th>팝업형태</th>
                <td>
                    <div class="radio__items">
                    <input type="radio" name="nomal" checked><span>일반팝업</span><input type="radio" name="nomal">레이어팝업
                    </div>
                </td>
            </tr>
            <tr>
                <th>사용여부</th>
                <td>
                    <div class="radio__items">
                       <input type="radio" name="yesno" checked><span>사용안함</span><input type="radio" name="yesno">사용안함
                    </div>
                </td>
                <th>스크롤여부</th>
                <td>
                    <div class="radio__items">
                       <input type="radio" name="yesno"><span>허용함</span><input type="radio" name="yesno" checked>허용안함
                    </div>
                </td>
            </tr>
            <tr>
                <th>위치*</th>
                <td>
                    X : <input type="text" value="100">
                    Y : <input type="text" value="100">
                </td>
                <th>크기</th>
                <td>
                    가로 : <input type="text" value="340">
                    세로 : <input type="text" value="400">
                </td>
            </tr>
            <tr>
                <th>링크주소</th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th>팝업내용</th>
                <td>
                    <Editor/>
                </td>
            </tr>
        </table>
        <div class="button__section">
            <div>
                <button @click="hyper('popuplist')">확인</button>
                <button @click="hyper('popuplist')">취소</button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuInfo from '../SideMenuInfo'
import Header from '../Header'
import Editor from '../Editor'

export default {
  name: 'PopupAdmin',
  components: {
    SideMenuInfo,
    Header,
    Editor
  },
  data () {
    return {}
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 table {
    margin-top: 10px;
 }
 table > tr > th {
     width: 300px;
 }
 table > tr > td {
     width: 600px;
 }
.tableContent {
    width: 100%;
    text-align: center;
}
.Mail > td input {
    width: 50px;
    margin-right: 10px;
}
.Mail > td button {
    border-radius: 5px;
    width: 100px;
}
.address > input {
    width: 400px;
}
</style>



