t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMember/>
      </div>
      <div class="section">
        <div class="title">
          <h3>회원관리</h3>
          <span>회원을 검색/수정 관리합니다.</span>
        </div>
        <table class="search">
          <tr>
            <th>조건검색</th>
            <td>
              <select>
                <option>::등급선택::</option>
                <option>일반회원</option>
                <option>광고회원</option>
              </select>
              <select>
                <option>고객명</option>
                <option>아이디</option>
                <option>이메일</option>
                <option>전화번호</option>
                <option>휴대폰</option>
                <option>추천인</option>
                <option>최초 로그인 ip</option>
                <option>마지막 로그인 ip</option>
              </select>
              <input type="text">
              <button>검색</button>
            </td>
          </tr>
        </table>
        <div class="tableTop">
          <div class="tabelTopLeft"><span>총 회원수 : 1, 검색 회원수 : 1</span></div>
          <div class="tabelTopRight">
            <button>엑셀파일저장</button>
            <button @click="hyper('MemberAdd')">회원등록</button>
          </div>
        </div>
        <table>
            <tr>
                <th width="5%"><input type="checkbox"></th>
                <th width="5%">번호</th>
                <th width="10%">이름</th>
                <th width="10%">아이디</th>
                <th width="10%">휴대폰</th>
                <th width="5%">SMS발송</th>
                <th width="5%">LMS발송</th>
                <th width="5%">MMS발송</th>
                <th width="5%">캐쉬</th>
                <th width="5%">방문수</th>
                <th width="10%">가입일</th>
                <th>마지막 로그인 IP</th>
                <th width="5%">승인</th>
                <th width="10%">기능</th>
            </tr>
            <tr v-for="(q, index) in list" :key="index">
                <td><input type="checkbox"></td>
                <td>{{ q.number }}</td>
                <td>{{ q.name }}</td>
                <td>{{ q.id }}</td>
                <td>{{ q.phone }}</td>
                <td>{{ q.sms }}건</td>
                <td>{{ q.lms }}건</td>
                <td>{{ q.mms }}건</td>
                <td>{{ q.cash }}p</td>
                <td>{{ q.visit }}</td>
                <td>{{ q.join }}</td>
                <td>{{ q.ip }}</td>
                <td>{{ q.approval }}</td>
                <td>
                    <button @click="hyper('MemberView')">보기</button>
                </td>
            </tr>
        </table>
        <div class="tableBottom">
          <div class="tableBottomLeft">
              <button @click="hyper('')">회원삭제</button>
              <button @click="sms">SMS발송</button>
          </div>
        </div>
        <div class="tableBottomPaging">
          <div class="tableBottomCenter">
            <img src="../../assets/btn_prev2.gif">
            <img src="../../assets/btn_prev.gif">
            <p>1/</p>
            <img src="../../assets/btn_next.gif">
            <img src="../../assets/btn_next2.gif">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMember from '../SideMenuMember'
import Header from '../Header'

export default {
  name: 'MemberList',
  components: {
    SideMenuMember,
    Header
  },
  data () {
    return {
      list: [
        {
          number: '1',
          name: '김영주',
          id: 'angelofsky',
          phone: '010-1122-1122',
          sms: '10',
          lms: '5',
          mms: '2',
          cash: '11.225',
          visit: '250',
          join: '2021-06-15',
          ip: '',
          approval: 'Y'
        }
      ]
    }
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    },
    sms () {
      window.open('/#/smsPopup', 'a', 'width=510, height=370, left=700, top=200')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.tableTop {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
  margin-bottom: 5px;
}
.tabelTopLeft > span {
  font-size: 13px;
  font-weight: bold;
}
table {
    text-align: center;
}
.tableBottom {
    margin-top: 10px;
    display: flex;
    width: 100%;
}
.search > tr > th {
  width: 200px;
}
.search > tr > td {
  text-align: left;
}
</style>



