t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMessage/>
      </div>
      <div class="section">
        <div class="title">
          <h3>충전금액목록</h3>
          <span>충전금액을 추가/수정/삭제 합니다.</span>
        </div>
        <table>
            <tr>
                <th width="5%">번호</th>
                <th>충전금액</th>
                <th>보너스</th>
                <th>첫결제보너스</th>
                <th>마일리지</th>
                <th width="10%">기능</th>
            </tr>
            <tr v-for="(q, index) in list" :key="index">
                <td>{{ q.number }}</td>
                <td>{{ q.charging }}원</td>
                <td>{{ q.bonus }}원</td>
                <td>{{ q.firstbunus }}원</td>
                <td>{{ q.mileage }}원</td>
                <td>
                    <button @click="hyper('ChargingSetting')">수정</button>
                    <button @click="del(index)">삭제</button>
                </td>
            </tr>
        </table>
        <div class="tableBottom">
            <div class="tableBottomLeft">
                <button @click="hyper('ChargingSetting')">등록하기</button>
            </div>
        </div>
        <div class="tableBottomPaging">
            <div class="tableBottomCenter">
              <img src="../../assets/btn_prev2.gif">
              <img src="../../assets/btn_prev.gif">
              <p>1/</p>
              <img src="../../assets/btn_next.gif">
              <img src="../../assets/btn_next2.gif">
            </div>
          </div>
        <div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMessage from '../SideMenuMessage'
import Header from '../Header'

export default {
  name: 'ChargingList',
  components: {
    SideMenuMessage,
    Header
  },
  data () {
    return {
      list: [
        {
          number: '1',
          charging: '1',
          bonus: '0',
          firstbunus: '0',
          mileage: '0'
        }
      ]
    }
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    },
    del (index) {
      this.list.splice(index, 1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
table {
    text-align: center;
}
.tableBottom {
  margin-top: 10px;
}
</style>



