t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMessage/>
      </div>
      <div class="section">
        <div class="title">
          <h3>충전금액목록</h3>
          <span>충전금액을 추가/수정/삭제 합니다.</span>
        </div>
        <table>
            <tr>
                <th>충전금액*</th>
                <td>
                    <input type="text" value="2000000">
                </td>
            </tr>
            <tr>
                <th>보너스</th>
                <td>
                    <input type="text" value="0">
                </td>
            </tr>
            <tr>
                <th>첫결제보너스</th>
                <td>
                    <input type="text" value="0">
                </td>
            </tr>
            <tr>
                <th>마일리지</th>
                <td>
                    <input type="text" value="0">
                </td>
            </tr>
            <tr>
                <th>단문 단가</th>
                <td>
                    <input type="text" value="7.8">
                </td>
            </tr>
            <tr>
                <th>장문 단가</th>
                <td>
                    <input type="text" value="24">
                </td>
            </tr>
            <tr>
                <th>이미지 단가</th>
                <td>
                    <input type="text" value="57">
                </td>
            </tr>
            <tr>
                <th>광고단문 단가</th>
                <td>
                    <input type="text" value="">
                </td>
            </tr>
            <tr>
                <th>광고장문 단가</th>
                <td>
                    <input type="text" value="">
                </td>
            </tr>
            <tr>
                <th>광고이미지 단가</th>
                <td>
                    <input type="text" value="">
                </td>
            </tr>
        </table>
        <div class="button__section">
            <div>
                <button @click="hyper('ChargingList')">확인</button>
                <button @click="hyper('ChargingList')">취소</button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMessage from '../SideMenuMessage'
import Header from '../Header'

export default {
  name: 'ChargingSetting',
  components: {
    SideMenuMessage,
    Header
  },
  data () {
    return {}
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 table {
    margin-top: 10px;
 }
 table > tr > th {
     width: 250px;
 }
.tableContent {
    width: 100%;
    text-align: center;
}
</style>



