<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuBanner/>
      </div>
      <div class="section">
        <div class="title">
          <h3>배너그룹생성</h3>
          <span>배너그룹을 추가/수정/삭제 관리합니다.</span>
        </div>
        <div class="section__top">
          <table>
            <tr>
              <th class="th__size">코드(영문)</th>
              <td class="td__size">
                <input type="text">
              </td>
              <th class="th__size">그룹이름</th>
              <td class="td__size">
                <input type="text">
              </td>
            </tr>
            <tr>
              <th class="th__size">배너형태</th>
              <td class="td__size">
                <div class="radio__items">
                  <input type="radio" name="type">
                  <span>세로형</span>
                  <input type="radio" name="type">
                  가로형
                </div>
              </td>
              <th class="th__size">배너간격</th>
              <td class="td__size">
                <input type="text">
              </td>
            </tr>
            <tr>
              <th class="th__size">배너형태</th>
              <td class="td__size">
                <div class="radio__items">
                  <input type="radio" name="use">
                  <span>사용함</span>
                  <input type="radio" name="use">
                  사용안함
                </div>
              </td>
              <th class="th__size">배너간격</th>
              <td class="td__size">
                <select>
                  <option value="">1</option>
                  <option value="">2</option>
                  <option value="">3</option>
                  <option value="">4</option>
                  <option value="">5</option>
                  <option value="">6</option>
                  <option value="">7</option>
                  <option value="">8</option>
                  <option value="">9</option>
                </select>
              </td>
            </tr>
          </table>
        </div>
        <div class="button__section">
            <div>
                <button @click="check">확인</button>
                <button @click="back">취소</button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../Header'
import SideMenuBanner from '../SideMenuBanner'

export default {
  name: 'BannerMain',
  components: {
    Header,
    SideMenuBanner
  },
  data () {
    return {
    }
  },
  watch: {
  },
  methods: {
    check () {
      this.$router.push('BannerMain')
    },
    back () {
      this.$router.go(-1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.section__top {
    display: flex;
    justify-content: space-between;
}
.th__size {
    width: 200px;
}
.td__size {
    text-align: left;
}
</style>



