<template>
  <div class="header">
    <div class="header__top">
        <a @click="hyper('SiteInfo')">
        <img src="https://www.munjamart.com/admin/data/config/admin_logo.gif">
        </a>
        <div class="header__top__right">
            <button @click="home">내홈페이지</button>
            <button @click="main">관리자메인</button>
            <button @click="hyper('/')">로그아웃</button>
        </div>
    </div>
    <div class="header__bottom">
        <nav class="header__bottom__gnb">
            <ul>
                <li>
                    <a @click="hyper('SiteInfo')">기본설정</a>
                </li>
                <li>
                    <a @click="hyper('Notice')">게시판관리</a>
                </li>
                <li>
                    <a @click="hyper('pagelist')">페이지관리</a>
                </li>
                <li>
                    <a @click="hyper('BannerMain')">배너관리</a>
                </li>
                <li>
                    <a @click="hyper('Statistics')">접속통계</a>
                </li>
                <li>
                    <a @click="hyper('memberlist')">회원관리</a>
                </li>
                <li>
                    <a @click="hyper('Payment')">결제관리</a>
                </li>
                <li>
                    <a @click="hyper('SmsSetting')">문자설정</a>
                </li>
                <li>
                    <a @click="hyper('SendSms')">발송내역</a>
                </li>
            </ul>
        </nav>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
    }
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    },
    main () {
      this.$router.push('siteinfo')
    },
    home () {
      window.location = 'https://draos15.github.io/'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
button {
    cursor: pointer;
    color: #000;
}
.header {
    min-width: 940px;
}
.header__top {
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.header__top > img {
    width: 293px;
    height: 20px;
}
.header__top > a {
    cursor: pointer;
}
.header__top button {
    width: 85px;
    height: 25px;
    border: 1px solid #ccc;
    border-radius: 7px;
    background: linear-gradient(#fff, #d3d3d3);
}
.header__bottom__gnb {
    width: 100%;
    height: 40px;
    background: linear-gradient(#66bb6a, #43a047);
    border-radius: 5px;
    margin-top: 15px;
}
.header__bottom__gnb > ul {
    display: flex;
    width: 950px;
    height: 100%;
    align-items: center;
    justify-content: space-around;
}
.header__bottom__gnb > ul > li > a {
    display: block;
    font-size: 15px;
    font-weight: 700;
    color: #fff;
    cursor: pointer;
}
.header__bottom__gnb > ul > li > a:hover {
  color: #ccc;
  transform: scaleX(1.1);
  transition: .1s;
}
</style>



