<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuPay/>
      </div>
      <div class="section">
        <div class="title">
          <h3>충전정보</h3>
          <span>충전정보 목록입니다.</span>
        </div>
        <div class="section__top">
            <table>
                <tr>
                    <th width="15%">주문번호</th>
                    <th>상품명</th>
                    <th width="10%">상품가격</th>
                    <th width="8%">수량</th>
                    <th width="8%">적립금</th>
                    <th width="10%">취소</th>
                </tr>
                <tr>
                    <td>210528171037465</td>
                    <td>문자발송 패키지</td>
                    <td>10,000원</td>
                    <td>1</td>
                    <td>10P</td>
                    <td>
                        <button>취소</button>
                    </td>
                </tr>
                <tr>
                    <td class="td__cost">배송비 :</td>
                    <td colspan="5" class="td__pay">상품합계( 0원 ) = 총 결제금액 : 10,000원</td>
                </tr>
            </table>
            <table>
                <tr>
                    <th class="th__size">주문번호</th>
                    <td class="td__size">210528171037465</td>
                    <th class="th__size">결제방법</th>
                    <td class="td__size">가상계좌</td>
                </tr>
                <tr>
                    <th class="th__size">주문일자</th>
                    <td class="td__size">2021-00-00 00:00:00</td>
                    <th class="th__size">에스크로여부</th>
                    <td class="td__size">N</td>
                </tr>
                <tr>
                    <th class="th__size">결제계좌</th>
                    <td class="td__size"></td>
                    <th class="th__size">입금인</th>
                    <td class="td__size"></td>
                </tr>
                <tr>
                    <th>처리상태</th>
                    <td>
                        <select name="" id="">
                            <option value="">------------</option>
                            <option value="">결제완료</option>
                            <option value="">------------</option>
                            <option value="">취소요청</option>
                            <option value="">취소완료</option>
                            <option value="">교환요청</option>
                            <option value="">교환완료</option>
                        </select>
                    </td>
                </tr>
                <tr>
                    <th>처리시간</th>
                    <td colspan="3">
                        <table>
                            <tr>
                                <th class="th__size1">주문접수</th>
                                <th class="th__size1">결제완료</th>
                                <th class="th__size1">주문취소</th>
                            </tr>
                            <tr>
                                <td class="td__size1">2021-00-00 00:00:00</td>
                                <td class="td__size1">-</td>
                                <td class="td__size1">-</td>
                            </tr>
                        </table>
                    </td>
                </tr>
            </table>
        </div>
        <div class="section__middle">
            <span>주문자 정보</span>
            <table>
                <tr>
                    <th class="th__size">주문자명</th>
                    <td class="td__size">
                        <input type="text">
                    </td>
                    <th class="th__size">이메일</th>
                    <td class="td__size">
                        <input type="text">
                        <button>발송</button>
                    </td>
                </tr>
                <tr>
                    <th class="th__size">전화번호</th>
                    <td class="td__size">
                        <input type="text">
                    </td>
                    <th class="th__size">휴대폰</th>
                    <td class="td__size">
                        <input type="text">
                        <button>발송</button>
                    </td>
                </tr>
                <tr>
                    <th>우편번호</th>
                    <td>
                        <input type="text" class="area__input"> -
                        <input type="text" class="area__input">
                        <button @click="mail">우편번호검색</button>
                    </td>
                </tr>
                <tr>
                    <th>주소</th>
                    <td>
                        <input type="text">
                    </td>
                </tr>
                <tr>
                    <th>요청사항</th>
                    <td colspan="3">
                        <textarea cols="65" rows="6"></textarea>
                    </td>
                </tr>
                <tr>
                    <th>주문취소 사유</th>
                    <td colspan="3">
                        <textarea cols="65" rows="6"></textarea>
                    </td>
                </tr>
                <tr>
                    <th>관리자 메모</th>
                    <td colspan="3">
                        <textarea cols="65" rows="6"></textarea>
                    </td>
                </tr>
            </table>
        </div>
        <div class="section__bottom">
            <span>증빙서류 정보</span>
            <table>
                <tr>
                    <th class="th__size2">발급여부</th>
                    <td colspan="3" class="td__size2">
                        <div class="radio__items">
                            <input type="radio" name="select" checked>
                            <span>발행안함</span>
                            <input type="radio" name="select">
                            <span>세금계산서 신청</span>
                            <input type="radio" name="select">
                            현금영수증 신청
                        </div>
                    </td>
                </tr>
            </table>
        </div>
        <div class="button__section">
            <div>
                <button>확인</button>
                <button @click="back">취소</button>
            </div>
        </div>
      </div>
    </div>
