</template>

<script>
import Header from '../Header'
import SideMenuPay from '../SideMenuPay'

export default {
  name: 'PaymentMore',
  components: {
    Header,
    SideMenuPay
  },
  data () {
    return {
    }
  },
  watch: {
  },
  methods: {
    mail () {
      window.open('/#/MailPopup', 'a', 'width=410, height=370, left=700, top=200')
    },
    back () {
      this.$router.push('Payment')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.td__cost {
    text-align: left;
}
.td__pay {
    text-align: right;
}
.th__size {
    width: 100px;
}
.td__size {
    width: 200px;
}
.th__size1 {
    width: 33%;
    text-align: center;
}
.td__size1 {
    text-align: center;
}
.section__middle {
    margin-top: 15px;
}
.section__bottom {
    margin-top: 15px;
}
.area__input {
    width: 50px;
}
.text__input {
    margin: 5px 0px;
    width: 100%;
    height: 80px;
}
.th__size2 {
    width: 210.33px;
}
</style>



