<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuBanner/>
      </div>
      <div class="section">
        <div class="title">
          <h3>메인배너</h3>
          <span>배너그룹을 추가/수정/삭제 관리합니다.</span>
        </div>
        <div class="section__table">
          <table>
            <tr>
              <th class="th__size">디자인 방법</th>
              <td class="td__size">
                <select>
                  <option value="">이미지</option>
                  <option value="">HTML</option>
                </select>
              </td>
              <th class="th__size">사용여부</th>
              <td class="td__size">
                <div class="radio__items">
                  <input type="radio" name="use">
                  <span>사용함</span>
                  <input type="radio" name="use" checked>
                  사용안함
                </div>
              </td>
            </tr>
            <tr>
              <th class="th__size">배너그룹</th>
              <td class="td__size">
                <select>
                  <option value="">메인배너</option>
                  <option value="">상단로고</option>
                </select>
              </td>
              <th class="th__size">우선순위</th>
              <td class="td__size">
                <select>
                  <option value="">1</option>
                  <option value="">2</option>
                  <option value="">3</option>
                  <option value="">4</option>
                  <option value="">5</option>
                  <option value="">6</option>
                  <option value="">7</option>
                  <option value="">8</option>
                  <option value="">9</option>
                  <option value="">10</option>
                </select>
              </td>
            </tr>
            <tr>
              <th class="th__size">링크주소</th>
              <td class="td__size">
                <input type="text">
                <input type="checkbox">
                <span>새창으로</span>
              </td>
            </tr>
            <tr>
              <th class="th__size"></th>
              <td class="td__size">
                <input type="file">
              </td>
            </tr>
            <tr>
              <th class="th__size">배너내용</th>
              <td colspan="3">
                <Editor/>
              </td>
            </tr>
          </table>
          <div class="button__section">
            <div>
                <button @click="check">확인</button>
                <button @click="close">취소</button>
            </div>
        </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../Header'
import SideMenuBanner from '../SideMenuBanner'
import Editor from '../Editor'

export default {
  name: 'BannerMainEdit',
  components: {
    Header,
    SideMenuBanner,
    Editor
  },
  data () {
    return {
    }
  },
  watch: {
  },
  methods: {
    check () {
      this.$router.push('BannerMain')
    },
    close () {
      this.$router.go(-1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.section__top {
  display: flex;
  justify-content: space-between;
}
.section__top__left > span {
  font-size: 13px;
  font-weight: bold;
}
.th__size {
  width: 300px;
}
.td__size {
  width: 300px;
}
</style>



