t<template>
  <div class="mailWrap">
      <table>
          <tr>
              <th>SMS발송</th>
          </tr>
          <tr>
              <th>발신자</th>
              <td><input type="text" value="01011221122" class="input"></td>
          </tr>
          <tr>
              <th>수신자</th>
              <td><textarea cols="50" rows="3"></textarea></td>
          </tr>
          <tr>
              <th>내용</th>
              <td><textarea cols="50" rows="10"></textarea></td>
          </tr>
      </table>
      <div class="button__section">
            <div>
                <button @click="check">발송하기</button>
                <button @click="close">취소</button>
            </div>
        </div>
  </div>
</template>

<script>

export default {
  name: 'SmsPopup',
  data () {
    return {
    }
  },
  methods: {
    check () {
      alert('발송되었습니다.')
      window.open('about:blank', '_self').close()
    },
    close () {
      window.open('about:blank', '_self').close()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .mailWrap {
  padding: 10px;
}
input {
    width: 369px;
}
 table {
    margin-top: 10px;
 }
 table > tr > th {
 }
 table > tr > td {
 }
</style>



