t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuInfo/>
      </div>
      <div class="section">
        <div class="title">
          <h3>관리자목록</h3>
          <span>관리자를 추가/수정/삭제 합니다.</span>
        </div>
        <table>
            <tr>
                <th>아이디*</th>
                <td>
                    <input type="text" value="admin">
                </td>
                <th>비밀번호*</th>
                <td>
                    <input type="password">
                </td>
            </tr>
            <tr>
                <th>이름*</th>
                <td>
                    <input type="text" value="관리자">
                </td>
                <th>이메일*</th>
                <td>
                    <input type="text" value="dd.dd@dd.com">
                </td>
            </tr>
            <tr>
                <th>주민번호</th>
                <td class="residentNumber">
                    <input type="text"> - <input type="text">
                </td>
                <th>전화번호</th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th>휴대폰</th>
                <td>
                    <input type="text">
                </td>
                <th>회원등급</th>
                <td>관리자</td>
            </tr>
            <tr class="Mail">
                <th>우편번호</th>
                <td>
                    <input type="text"> - <input type="text">
                    <button @click="mail">우편번호 검색</button>
                </td>
            </tr>
            <tr>
                <th>주소</th>
                <td class="address">
                    <input type="text"><br>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th>관리자 허용 IP 사용여부</th>
                <td>
                    <div class="radio__items">
                     <input type="radio" name="yesno"><span>사용</span><input type="radio" name="yesno" checked>미사용
                    </div>
                </td>
            </tr>
            <tr>
                <th>관리자 허용 IP</th>
                <td>
                    <textarea cols="80" rows="5"></textarea>
                </td>
            </tr>
            <tr>
                <th>메모</th>
                <td>
                    <textarea cols="80" rows="5"></textarea>
                </td>
            </tr>
        </table>
        <div class="button__section">
            <div>
                <button @click="hyper('adminlist')">확인</button>
                <button @click="hyper('adminlist')">취소</button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuInfo from '../SideMenuInfo'
import Header from '../Header'

export default {
  name: 'AdminList',
  components: {
    SideMenuInfo,
    Header
  },
  data () {
    return {}
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 table {
    margin-top: 10px;
 }
 table > tr > th {
     width: 300px;
 }
 table > tr > td {
     width: 600px;
 }
.tableContent {
    width: 100%;
    text-align: center;
}
.Mail > td input {
    width: 50px;
}
.Mail > td button {
    border-radius: 5px;
    width: 100px;
}
.residentNumber > input {
    width: 83px;
}
.address > input {
    width: 400px;
}
</style>



