<template>
    <tinymce id="d1" :other_options="tinyOptions"></tinymce>
</template>

<script>
import tinymce from 'vue-tinymce-editor'

export default {
  name: 'Editor',
  components: {
    tinymce
  },
  data () {
    return {
      tinyOptions: {
        'width': '100%',
        'height': '400px'
      }
    }
  }
}
</script>



