<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuStatistics/>
      </div>
      <div class="section">
        <div class="title">
          <h3>충전분석</h3>
          <span>충전금액을 일별/월별/년별 로 분석합니다.</span>
        </div>
        <div class="section__top">
          <table>
            <tr>
              <th class="th__size">분석방법</th>
              <td @change="change" class="td__size">
                <input type="radio" name="qq" id="0" value="0" v-model="check"><label for="0">일별</label>
                <input type="radio" name="qq" id="1" value="1" v-model="check"><label for="1">월별</label>
                <input type="radio" name="qq" id="2" value="2" v-model="check"><label for="2">년별</label>
              </td>
            </tr>
            <tr>
              <th class="th__size">기간</th>
              <td class="td__size">
                <select name="" id="">
                  <option value="">2019</option>
                  <option value="">2020</option>
                  <option value="">2021</option>
                </select>
                <span>년</span>
                <select name="" id="">
                  <option v-for="m in 12" v-bind:key="m" :value="m">{{ 10 > m ? '0' + m : m }}</option>
                </select>
                <span>월</span>
                <select name="" id="">
                <option v-for="m in 31" v-bind:key="m" :value="m">{{ 10 > m ? '0' + m : m }}</option>
                </select>
                <span>일 ~</span>
                <select name="" id="">
                  <option value="">2019</option>
                  <option value="">2020</option>
                  <option value="">2021</option>
                </select>
                <span>년</span>
                <select name="" id="">
                  <option v-for="m in 12" v-bind:key="m" :value="m">{{ 10 > m ? '0' + m : m }}</option>
                </select>
                <span>월</span>
                <select name="" id="">
                <option v-for="m in 31" v-bind:key="m" :value="m">{{ 10 > m ? '0' + m : m }}</option>
                </select>
                <span>일</span>
                <button>검색</button>
                <button>접속자분석 초기화</button>
              </td>
            </tr>
          </table>
        </div>
        <div class="section__middle">
          <div>
            <GChart
              type="ColumnChart"
              :data="chartData"
              :options="chartOptions"
              value="test"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../Header'
import SideMenuStatistics from '../SideMenuStatistics'
import { GChart } from 'vue-google-charts'

export default {
  name: 'Statistics',
  components: {
    Header,
    SideMenuStatistics,
    GChart
  },
  data () {
    return {
      check: '0',
      chartData: [
        ['날짜', '충전금액'],
        ['1일', 2000000],
        ['2일', 2500000],
        ['3일', 1000000],
        ['4일', 1850000],
        ['5일', 2760000],
        ['6일', 3000000],
        ['7일', 2000000],
        ['8일', 2500000],
        ['9일', 1000000],
        ['10일', 1850000],
        ['11일', 2760000],
        ['12일', 3000000],
        ['13일', 2000000],
        ['14일', 2500000],
        ['15일', 1000000],
        ['16일', 1850000],
        ['17일', 2760000],
        ['18일', 3000000],
        ['19일', 2000000],
        ['20일', 2500000],
        ['21일', 1000000],
        ['22일', 1850000],
        ['23일', 2760000],
        ['24일', 3000000],
        ['25일', 2000000],
        ['26일', 2500000],
        ['27일', 1000000],
        ['28일', 1850000],
        ['29일', 2760000],
        ['30일', 3000000],
        ['31일', 2000000]
      ],
      chartOptions: {
        chart: {
          title: 'Company Performance',
          subtitle: 'Sales, Expenses, and Profit: 2014-2017'
        }
      }
    }
  },
  methods: {
    change () {
      const rowData = [
        ['날짜', '충전금액'],
        ['1일', 2000000],
        ['2일', 2500000],
        ['3일', 1000000],
        ['4일', 1850000],
        ['5일', 2760000],
        ['6일', 3000000],
        ['7일', 2000000],
        ['8일', 2500000],
        ['9일', 1000000],
        ['10일', 1850000],
        ['11일', 2760000],
        ['12일', 3000000],
        ['13일', 2000000],
        ['14일', 2500000],
        ['15일', 1000000],
        ['16일', 1850000],
        ['17일', 2760000],
        ['18일', 3000000],
        ['19일', 2000000],
        ['20일', 2500000],
        ['21일', 1000000],
        ['22일', 1850000],
        ['23일', 2760000],
        ['24일', 3000000],
        ['25일', 2000000],
        ['26일', 2500000],
        ['27일', 1000000],
        ['28일', 1850000],
        ['29일', 2760000],
        ['30일', 3000000],
        ['31일', 2000000]
      ]

      const rowDataB = [
        ['날짜', '방문자수'],
        ['1월', 2760000],
        ['2월', 1850000],
        ['3월', 2760000],
        ['4월', 1850000],
        ['5월', 2760000],
        ['6월', 2760000],
        ['7월', 1850000],
        ['8월', 2760000],
        ['9월', 1850000],
        ['10월', 3760000],
        ['11월', 2760000],
        ['12월', 4760000]
      ]

      const rowDataC = [
        ['날짜', '방문자수'],
        ['2018년', 47600000],
        ['2019년', 27600000],
        ['2020년', 18500000],
        ['2021년', 13912414]
      ]

      switch (this.check) {
        case '0':
          this.chartData = rowData
          break
        case '1':
          this.chartData = rowDataB
          break
        case '2':
          this.chartData = rowDataC
          break
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.section__top {
  display: flex;
  justify-content: space-between;
}
.section__top__left > span {
  font-size: 13px;
  font-weight: bold;
}
.section__middle {
  margin-top: 15px
}
.seciton__middle__table {
  margin-top: 5px;
  text-align: center;
}
.th__size {
  width: auto;
}
</style>



