t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMessage/>
      </div>
      <div class="section">
        <div class="title">
          <h3>SMS기본정보</h3>
          <span>SMS 기본정보를 관리합니다.</span>
        </div>
        <table>
            <tr>
                <th>기본정보</th>
            </tr>
            <tr>
                <th>금지어</th>
                <td>
                    <textarea cols="60" rows="13"></textarea>
                </td>
            </tr>
            <tr>
                <th>상단검색어</th>
                <td><input type="text"></td>
            </tr>
            <tr>
                <th>동시 발송 건수</th>
                <td><input type="text" value="0"></td>
            </tr>
            <tr>
                <th>심야 발송 건수</th>
                <td><input type="text" value="0"></td>
            </tr>
            <tr>
                <th>SMS 발송비용(일반)</th>
                <td><input type="text" value="7.8"></td>
            </tr>
            <tr>
                <th>LMS 발송비용(일반)</th>
                <td><input type="text" value="24"></td>
            </tr>
            <tr>
                <th>MMS 발송비용(일반)</th>
                <td><input type="text" value="57"></td>
            </tr>
            <tr>
                <th>SMS 발송비용(광고)</th>
                <td><input type="text" value="20"></td>
            </tr>
            <tr>
                <th>LMS 발송비용(광고)</th>
                <td><input type="text" value="40"></td>
            </tr>
            <tr>
                <th>MMS 발송비용(광고)</th>
                <td><input type="text" value="110"></td>
            </tr>
        </table>
        <table>
            <tr>
                <th>SSL 사용</th>
            </tr>
            <tr>
                <th>사용여부</th>
                <td>
                    <div class="radio__items">
                        <input type="radio" name="yes"><span>사용함</span><input type="radio" name="yes">사용안함
                    </div>
                </td>
            </tr>
            <tr>
                <th>포트번호</th>
                <td><input type="text"></td>
            </tr>
        </table>
        <table>
            <tr>
                <th>
                    -SSL을 사용하는경우 기본적으로 서버에 SSL이 적용이 되어 있어야 합니다.<br>
                    -확인 방법 https://해당도메인 ex) https://www.munja.com
                </th>
            </tr>
        </table>
        <table>
            <tr>
                <th>회원가입시 무료포인트</th>
            </tr>
            <tr>
                <th>사용여부</th>
                <td>
                    <div class="radio__items">
                    <input type="radio" name="yesno" checked><span>사용함</span><input type="radio" name="yesno">사용안함
                    </div>
                </td>
            </tr>
            <tr>
                <th>무료포인트</th>
                <td><input type="text" value="100"></td>
            </tr>
        </table>
        <div class="button__section">
            <div>
                <button @click="hyper('')">확인</button>
                <button @click="hyper('')">취소</button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMessage from '../SideMenuMessage'
import Header from '../Header'

export default {
  name: 'SmsSetting',
  components: {
    SideMenuMessage,
    Header
  },
  data () {
    return {}
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 table {
    margin-top: 10px;
 }
 table > tr > th {
     width: 250px;
 }
 table > tr:nth-child(1) > th {
     background: #fff;
 }
.tableContent {
    width: 100%;
    text-align: center;
}
.Mail > td button {
    border-radius: 5px;
    width: 100px;
}
.Mail > td {
    line-height: 2;
}
.address {
    width: 400px;
}
.mailInput {
    width: 60px;
}
.td__size {
    width: 500px;
}
</style>



