<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuBanner/>
      </div>
      <div class="section">
        <div class="title">
          <h3>배너그룹관리</h3>
          <span>배너그룹을 추가/수정/삭제 관리합니다.</span>
        </div>
        <div class="section__top">
            <div class="section__top__left">
                <span>총 배너그룹수 :</span>
            </div>
            <div class="section__top__right">
                <button @click="add">배너그룹생성</button>
            </div>
        </div>
        <table>
            <tr>
                <th width="5%">번호</th>
                <th width="10%">코드</th>
                <th>이미지</th>
                <th>링크주소</th>
                <th width="10%">사용여부</th>
                <th width="10%">기능</th>
            </tr>
            <tr>
                <td colspan="6">작성된 내용이 없습니다.</td>
            </tr>
        </table>
        <div class="tableBottomPaging">
          <div class="tableBottomCenter">
            <img src="../../assets/btn_prev2.gif">
            <img src="../../assets/btn_prev.gif">
            <p>1/</p>
            <img src="../../assets/btn_next.gif">
            <img src="../../assets/btn_next2.gif">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../Header'
import SideMenuBanner from '../SideMenuBanner'

export default {
  name: 'BannerTopLogo',
  components: {
    Header,
    SideMenuBanner
  },
  data () {
    return {
      list: 'notice'
    }
  },
  watch: {
  },
  methods: {
    edit () {
      window.open('/#/PostEdit', 'a', 'width=1500, height=915, left=150, top=20')
    },
    add () {
      this.$router.push('BannerAdd')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.section__top {
    display: flex;
    justify-content: space-between;
}
.section__top__left > span {
    font-size: 13px;
    font-weight: bold;
}
table {
    margin-top: 10px;
    text-align: center;
}
</style>



