t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMember/>
      </div>
      <div class="section">
        <div class="title">
          <h3>회원등록</h3>
          <span>회원을 검색/수정 관리합니다.</span>
        </div>
        <table>
            <tr>
                <th>아이디</th>
                <td class="td__size">
                    <input type="text" value="">
                </td>
                <th>비밀번호</th>
                <td>
                    <input type="password">
                </td>
            </tr>
            <tr>
                <th>이름</th>
                <td class="td__size">
                    <input type="text" value="">
                </td>
                <th>회원등급</th>
                <td>
                    <select>
                        <option>일반회원</option>
                        <option>광고회원</option>
                    </select>
                </td>
            </tr>
            <tr>
                <th>발송모듈 설정</th>
                <td>
                    <select>
                        <option>원샷일반</option>
                        <option>광고일반</option>
                    </select>
                </td>
            </tr>
            <tr>
                <th>닉네임</th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th>최초 로그인 ip</th>
                <td class="td__size">
                    <span></span>
                </td>
                <th>마지막 로그인 ip</th>
                <td>
                    <span></span>
                </td>
            </tr>
            <tr>
                <th>sms 발송건수</th>
                <td class="td__size">
                    <span>0건</span>
                </td>
                <th>lms 발송건수</th>
                <td>
                    <span>0건</span>
                </td>
            </tr>
            <tr>
                <th>mms 발송건수</th>
                <td>
                    <span>0건</span>
                </td>
            </tr>
            <tr>
                <th>포인트</th>
                <td class="td__size">0 포인트 <button @click="point">상세보기</button></td>
                <th>승인</th>
                <td>
                    <div class="radio__items">
                      <input type="radio" name="yesno"><span>사용</span><input type="radio" name="yesno" checked>미사용
                    </div>
                </td>
            </tr>
            <tr>
                <th>sms발송비용</th>
                <td class="td__size"><input type="text" value="20">원</td>
                <th>lms발송비용</th>
                <td><input type="text" value="60">원</td>
            </tr>
            <tr>
                <th>mms발송비용</th>
                <td class="td__size"><input type="text" value="100">원</td>
                <th>동시발송건수</th>
                <td><input type="text" value="0"> *한번 전송시 제한건수입니다.</td>
            </tr>
            <tr>
                <th>충전</th>
                <td>
                    <div class="radio__items">
                        <input type="radio" name="yesno" checked><span>활성화</span><input type="radio" name="yesno">비활성화
                    </div>
                </td>
            </tr>
            <tr>
                <th>로그인 허용 IP</th>
                <td>
                    <textarea cols="70" rows="5"></textarea>
                </td>
            </tr>
            <tr>
                <th>총주문액</th>
                <td>0 원 <button @click="pay">상세보기</button></td>
            </tr>
            <tr>
                <th>전화번호</th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th>발신번호 관리</th>
                <td><button @click="number">상세보기</button></td>
            </tr>
            <tr>
                <th>휴대폰</th>
                <td>
                    <input type="text">
                </td>
            </tr>
            <tr>
                <th>이메일</th>
                <td><input type="text" value=""></td>
            </tr>
            <tr class="Mail">
                <th>주소</th>
                <td>
                    <input class="mailInput" type="text"> - <input class="mailInput" type="text">
                    <button @click="mail">우편번호 검색</button><br>
                    <input class="address" type="text"><br>
                    <input class="address" type="text">
                </td>
            </tr>
            <tr>
                <th>추천인</th>
                <td><input type="text"></td>
            </tr>
            <tr>
                <th>관리자 메모</th>
                <td>
                    <textarea cols="70" rows="5"></textarea>
                </td>
            </tr>
        </table>
        <div class="button__section">
            <div>
                <button @click="hyper('memberlist')">확인</button>
                <button @click="hyper('memberlist')">취소</button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMember from '../SideMenuMember'
import Header from '../Header'

export default {
  name: 'MemberAdd',
  components: {
    SideMenuMember,
    Header
  },
  data () {
    return {}
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    },
    mail () {
      window.open('/#/MailPopup', 'a', 'width=410, height=370, left=700, top=200')
    },
    point () {
      window.open('/#/PointPopup', 'a', 'width=670, height=370, left=700, top=200')
    },
    pay () {
      window.open('/#/paypopup', 'a', 'width=670, height=370, left=700, top=200')
    },
    number () {
      window.open('/#/NumberPopup', 'a', 'width=670, height=370, left=700, top=200')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 table {
    margin-top: 10px;
 }
 table > tr > th {
     width: 300px;
 }
 table > tr > td {
     width: 600px;
 }
.tableContent {
    width: 100%;
    text-align: center;
}
.Mail > td button {
    border-radius: 5px;
    width: 100px;
}
.Mail > td {
    line-height: 2;
}
.address {
    width: 400px;
}
.mailInput {
    width: 60px;
}
.td__size {
    width: 500px;
}
</style>



