t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMember/>
      </div>
      <div class="section">
        <div class="title">
          <h3>메세지설정</h3>
          <span>상황별 메일/SMS발송 내용을 관리합니다</span>
        </div>
        <table>
            <tr>
                <th width="5%">번호</th>
                <th width="20%">코드</th>
                <th>분류명</th>
                <th width="10%">발송여부</th>
                <th width="10%">기능</th>
            </tr>
            <tr v-for="(q, index) in list" :key="index">
                <td>{{ q.number }}</td>
                <td>{{ q.code }}</td>
                <td>{{ q.title }}</td>
                <td>{{ q.yes }}</td>
                <td><button @click="hyper('AddMessege')">수정</button></td>
            </tr>
        </table>
        <div class="buttonBox">
          <button @click="hyper('AddMessege')">메일등록</button>
        </div>
        <div class="tableBottomPaging">
          <div class="tableBottomCenter">
            <img src="../../assets/btn_prev2.gif">
            <img src="../../assets/btn_prev.gif">
            <p>1/</p>
            <img src="../../assets/btn_next.gif">
            <img src="../../assets/btn_next2.gif">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMember from '../SideMenuMember'
import Header from '../Header'

export default {
  name: 'MessageSetting',
  components: {
    SideMenuMember,
    Header
  },
  data () {
    return {
      list: [
        {
          number: '1',
          code: 'form',
          title: '폼메일 답변메일',
          yes: '발송함'
        }
      ]
    }
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
table {
    text-align: center;
}
table > tr > th:nth-child(3) {
  width: 700px;
}
.buttonBox {
  margin-top: 10px;
}
</style>



