t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMember/>
      </div>
      <div class="section">
        <div class="title">
          <h3>포인트로그</h3>
          <span>회원에게 부여된 포인트를 관리합니다.</span>
        </div>
        <table class="search">
          <tr>
            <th>조건검색</th>
            <td>
              <select>
                <option>전체</option>
                <option>회원가입</option>
                <option>로그인</option>
                <option>게시판 - 전체</option>
                <option>게시판 - 보기</option>
                <option>게시판 - 쓰기</option>
                <option>게시판 - 다운로드</option>
                <option>코멘트</option>
                <option>쪽지</option>
              </select>
              <select>
                <option>아이디</option>
                <option>포인트 내역</option>
              </select>
              <input type="text">
              <button>검색</button>
            </td>
          </tr>
        </table>
        <div class="tableTop">
          <div class="tabelTopLeft"><span>총 포인트수 : 1, 검색 포인트수 : 1</span></div>
        </div>
        <table>
            <tr>
                <th width="5%"><input type="checkbox"></th>
                <th width="5%">번호</th>
                <th width="10%">아이디</th>
                <th width="10%">포인트</th>
                <th>포인트내역</th>
                <th width="20%">적립입자</th>
                <th width="10%">기능</th>
            </tr>
            <tr v-for="(q, index) in list" :key="index">
                <td><input type="checkbox"></td>
                <td>{{ q.number }}</td>
                <td>{{ q.id }}</td>
                <td>{{ q.point }}</td>
                <td>{{ q.pointinfo }}</td>
                <td>{{ q.day }}</td>
                <td><button @click="del(index)">삭제</button></td>
            </tr>
        </table>
        <div class="tableBottom">
          <div class="tableBottomLeft">
            <button>선택삭제</button>
          </div>
        </div>
        <div class="tableBottomPaging">
          <div class="tableBottomCenter">
            <img src="../../assets/btn_prev2.gif">
            <img src="../../assets/btn_prev.gif">
            <p>1/</p>
            <img src="../../assets/btn_next.gif">
            <img src="../../assets/btn_next2.gif">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMember from '../SideMenuMember'
import Header from '../Header'

export default {
  name: 'PointList',
  components: {
    SideMenuMember,
    Header
  },
  data () {
    return {
      list: [
        {
          number: '1',
          id: 'angelofsky',
          point: '5,000',
          pointinfo: '관리자 충전',
          day: '2021-06-15 13:30:21'
        },
        {
          number: '2',
          id: 'sdwdq',
          point: '7,000',
          pointinfo: '관리자 충전',
          day: '2021-06-15 13:30:21'
        },
        {
          number: '3',
          id: 'azlzlzls',
          point: '121,000',
          pointinfo: '관리자 충전',
          day: '2021-06-15 13:30:21'
        }
      ]
    }
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    },
    del (index) {
      this.list.splice(index, 1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.tableTop {
  width: 100%;
  height: 24px;
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
  margin-bottom: 5px;
}
.tabelTopLeft > span {
  font-size: 13px;
  font-weight: bold;
}
table {
  text-align: center;
}
.tableBottom {
  margin-top: 10px;
}
.search > tr > th {
  width: 200px;
}
.search > tr > td {
  text-align: left;
}
</style>



