<template>
  <div>
    <div class="loginTop"></div>
    <div class="logincontainer">
      <div class="loginWrap">
        <div><img src="../../assets/login.gif"></div>
        <div class="imgCenter"><img src="../../assets/login_vline.gif"></div>
        <div class="loginBox">
          <div class="loginText">
            <div>ID</div>
            <div>PASSWORD</div>
          </div>
          <div class="loginInput">
            <div><input type="text"></div>
            <div><input type="password"></div>
            <div><img src="../../assets/login_txt.gif"></div>
          </div>
          <div class="loginButton">
            <button @click="main">로그인</button>
          </div>
        </div>
      </div>
    </div>
    <div class="loginBottom"></div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {}
  },
  methods: {
    main () {
      this.$router.push('siteinfo')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.loginTop {
  height: 200px;
  background-color: #f7f7f7;
}
.loginBottom {
  height: 450px;
  background-color: #f7f7f7;
}
.logincontainer {
  width: 100%;
  height: 280px;
  background-color: #fff;
  display: flex;
  border: 1px solid #ccc;
}
.loginWrap {
  width: 750px;
  display: flex;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
}
.imgCenter {
  margin: 0 30px;
}
.loginBox {
  width: 100%;
  display: flex;
  align-items: center;
}
.loginText {
  font-size: 11px;
  font-weight: bold;
  color: #080808;
  margin-right: 10px;
  line-height: 2.7;
}
.loginInput {
  line-height: 1.8;
}
.loginInput img {
  position: absolute;
  margin-top: 10px ;
}
.loginButton button {
  width: 60px;
  height: 50px;
  margin-top: 3px;
  margin-left: 10px;
}
</style>



