t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMessage/>
      </div>
      <div class="section">
        <div class="title">
          <h3>운영정보설정</h3>
          <span>운영에 필요한 정보를 설정합니다.</span>
        </div>
        <table>
            <tr>
                <th>결제정보</th>
            </tr>
            <tr>
                <th>결제방법</th>
                <td><input type="checkbox" name="box"> 무통장입금  <input type="checkbox" name="box"> 카드결제  <input type="checkbox" name="box"> 계좌이체
                <input type="checkbox" name="box"> 가상계좌  <input type="checkbox" name="box"> 휴대폰결제
                </td>
            </tr>
            <tr>
                <th>결제시스템</th>
                <td>
                    <table class="subTable">
                        <tr>
                            <th>
                                <div class="radio__items">
                                    <input type="radio" name="tt">테스트
                                </div>
                            </th>
                            <td>결제모듈을 테스트로 이용합니다.</td>
                        </tr>
                        <tr>
                            <th>
                                <div class="radio__items">
                                    <input type="radio" name="tt">PG업체 연동
                                </div>
                            </th>
                            <td class="textBox">
                                <div class="radio__items">
                                    <input type="radio" checked><span>LG유플러스 (http://www.ww.ww.ww)</span><button>제휴가입하기</button>
                                </div>
                            <span>
                            수수료(카드결제 => 3.1%, 계좌이체 2.0%, 가상계좌 400원)<br>
                            -위 제휴가입하기 버튼을 클릭해서 가입해야만 수수료 할인이 적용됩니다.<br>
                            -진행절차 ( 제휴가입 > 등록비결제 > 구비서류준비 > 등기발송) 문의 1555-5555<br>
                            주의1 : 제휴가입을 통해 가입해야 결제시스템이 정상작동 합니다.<br>
                            주의2 : 승인결과 전송 "결제창2.0"으로 변경<br>
                            -"LG유플러스 관리자 > 상점정보관리" > 승인 결과 전송 여부" 를 반드시 "결제창2.0" 으로 선택하세요
                            </span>
                            </td>
                        </tr>
                    </table>
                </td>
            </tr>
            <tr>
                <th>상점 ID</th>
                <td><input type="text"></td>
            </tr>
            <tr>
                <th>상점 mertkey</th>
                <td><input type="text"></td>
            </tr>
            <tr>
                <th>은행계좌번호</th>
                <td>
                    <table class="subTable">
                        <tr>
                            <td><button @click="bank">은행계좌번호등록</button></td>
                        </tr>
                        <tr>
                            <th>은행명</th>
                            <th>계좌번호</th>
                            <th>예금주</th>
                            <th>기능</th>
                        </tr>
                        <tr>
                            <td>KB국민은행</td>
                            <td>1121212-121211</td>
                            <td>이이이</td>
                            <td>
                                <button>수정</button>
                                <button>삭제</button>
                            </td>
                        </tr>
                    </table>
                </td>
            </tr>
        </table>
        <table>
            <tr>
                <th>가상계좌수신</th>
            </tr>
            <tr>
                <th>가상계좌 수신url</th>
                <td>
                    <table class="subTable">
                        <tr>
                            <th>LG데이콤</th>
                            <td>http://www.ww.ww.ww</td>
                        </tr>
                    </table>
                </td>
            </tr>
        </table>
        <table>
            <tr>
                <th>적립금정보</th>
            </tr>
            <tr>
                <th>사용여부</th>
                <td>
                    <div class="radio__items">
                        <input type="radio" name="qq"><span>사용함</span>
                        <input type="radio" name="qq" checked>사용안함
                    </div>
                </td>
            </tr>
            <tr>
                <th>회원가입 적립금</th>
                <td><input type="text" value="1000"></td>
                <th>추천인 적립금</th>
                <td><input type="text" value="0"></td>
            </tr>
            <tr>
                <th>최소사용 적립금</th>
                <td><input type="text" value="1000"></td>
                <th>1회 최대사용 적립금</th>
                <td><input type="text" value="50000"></td>
            </tr>
            <tr>
                <th>상품구매시 적립금</th>
                <td><input type="text" value="10"> %</td>
                <th>적립금 일괄적용</th>
                <td><input type="text" value="0"> % <button>적용</button></td>
            </tr>
        </table>
        <table>
            <tr>
                <th>세금계산서 설정</th>
            </tr>
            <tr>
                <th>사용여부</th>
                <td>
                    <div class="radio__items">
                        <input type="radio" name="ww" checked><span>사용함</span>
                        <input type="radio" name="ww">사용안함
                    </div>
                </td>
                <th>발급시점</th>
                <td>
                    <div class="radio__items">
                        <input type="radio" name="ee" checked><span>결제완료</span>
                        <input type="radio" name="ee">배송완료
                    </div>
                </td>
            </tr>
        </table>
        <div class="button__section">
            <div>
                <button>확인</button>
                <button>취소</button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMessage from '../SideMenuMessage'
import Header from '../Header'

export default {
  name: 'PaySetting',
  components: {
    SideMenuMessage,
    Header
  },
  data () {
    return {}
  },
  methods: {
    bank () {
      window.open('/#/BankPopup', 'a', 'width=400, height=300, left=400, top=300')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 table {
    margin-top: 10px;
 }
 table > tr:nth-child(1) > th {
    background: #fff;
 }
 .subTable > tr:nth-child(1) > th {
    background: linear-gradient( #c0e4c2, #abd0ae);
 }
 table > tr > th {
     width: 250px;
 }
 .td__size {
     width: 512.78px;
 }
 .textBox {
     background: #ccc;
 }
.tableContent {
    width: 100%;
    text-align: center;
}
</style>



