<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuPage/>
      </div>
      <div class="section">
        <div class="title">
          <h3>페이지관리</h3>
          <span>페이지를 추가/수정/삭제 관리합니다.</span>
        </div>
        <div class="section__top">
            <div class="section__top__left">
                <span>등록된 페이지 총 1개</span>
            </div>
            <div class="section__top__right">
                <button @click="hyper('pageadd')">페이지등록</button>
            </div>
        </div>
        <table>
            <tr>
              <th width="5%">번호</th>
              <th width="10%">코드</th>
              <th>페이지</th>
              <th>주소</th>
              <th width="10%">기능</th>
            </tr>
            <tr v-for="(q, index) in list" :key="index">
                <td>{{ q.number }}</td>
                <td>{{ q.code }}</td>
                <td>{{ q.page }}</td>
                <td>{{ q.address }}</td>
                <td>
                    <button @click="hyper('Term', q.number)">수정</button>
                    <button @click="del(index)">삭제</button>
                </td>
            </tr>
        </table>
        <div class="tableBottomPaging">
          <div class="tableBottomCenter">
            <img src="../../assets/btn_prev2.gif">
            <img src="../../assets/btn_prev.gif">
            <p>1/</p>
            <img src="../../assets/btn_next.gif">
            <img src="../../assets/btn_next2.gif">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuPage from '../SideMenuPage'
import Header from '../Header'

export default {
  name: 'Test',
  components: {
    SideMenuPage,
    Header
  },
  data () {
    return {
      list: []
    }
  },
  methods: {
    hyper (path, key) {
      console.log(path, key)
      this.$router.push({ name: path, params: { key: key } })
      this.$router.push(path)
    },
    del (index) {
      console.log(this.list, index)
      this.list.splice(index, 1)
    }
  },
  mounted () {
    this.$axios.get('https://web.peachkor.com/api/group')
      .then(res => {
        this.list = res.data.data
      })
      .catch(function (error) {
        console.log(error)
      })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.section__top {
  display: flex;
  justify-content: space-between;
}
.section__top__left > span {
  font-size: 13px;
  font-weight: bold;
}
table {
  margin-top: 10px;
  text-align: center;
}
</style>



