t<template>
  <div class="wrap">
    <Header/>
    <div class="article">
      <div class="side">
        <SideMenuMember/>
      </div>
      <div class="section">
        <div class="title">
          <h3>탈퇴회원</h3>
          <span>탈퇴회원을 조회 관리합니다.</span>
        </div>
        <table class="search">
          <tr>
            <th>조건검색</th>
            <td>
              <select>
                <option>고객명</option>
                <option>아이디</option>
              </select>
              <input type="text">
              <button>검색</button>
            </td>
          </tr>
        </table>
        <table>
            <tr>
                <th width="5%">번호</th>
                <th width="10%">회원명</th>
                <th>탈퇴사유</th>
                <th>충고내용</th>
                <th width="10%">탈퇴날짜</th>
                <th width="10%">기능</th>
            </tr>
            <tr v-for="(q, index) in list" :key="index">
                <td>{{ q.number }}</td>
                <td>{{ q.name }}</td>
                <td>{{ q.title }}</td>
                <td>{{ q.info }}</td>
                <td>{{ q.day }}</td>
                <td><button @click="del(index)">삭제</button></td>
            </tr>
        </table>
        <div class="tableBottomPaging">
          <div class="tableBottomCenter">
            <img src="../../assets/btn_prev2.gif">
            <img src="../../assets/btn_prev.gif">
            <p>1/</p>
            <img src="../../assets/btn_next.gif">
            <img src="../../assets/btn_next2.gif">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SideMenuMember from '../SideMenuMember'
import Header from '../Header'

export default {
  name: 'OutMember',
  components: {
    SideMenuMember,
    Header
  },
  data () {
    return {
      list: [
        {
          number: '1',
          name: '김영주',
          title: '탈퇴요',
          info: '힘힘',
          day: '2021-06-15 15:00:20'
        },
        {
          number: '2',
          name: '전영길',
          title: '손 씻어서 탈퇴합니다',
          info: '건승하세요',
          day: '2021-06-15 15:00:20'
        },
        {
          number: '3',
          name: '진홍민',
          title: '문자같은거 안씁니다 이제',
          info: '수고하세요',
          day: '2021-06-15 15:00:20'
        }
      ]
    }
  },
  methods: {
    hyper (path) {
      this.$router.push(path)
    },
    del (index) {
      this.list.splice(index, 1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
table {
    text-align: center;
}
.search {
  margin-bottom: 10px;
}
.search > tr > th {
  width: 200px;
}
.search > tr > td {
  text-align: left;
}
</style>



